.section {
  .container {
    padding-bottom: 5px;
  }

  .container--shadow {
    padding-bottom: 0;
  }
}

.section + .section {
  margin-top: 115px;

  @include vp-1023 {
    margin-top: 95px;
  }

  @include vp-767 {
    margin-top: 75px;
  }
}

.section:last-child {
  margin-bottom: 75px;

  @include vp-1023 {
    margin-bottom: 55px;
  }

  @include vp-767 {
    margin-bottom: 35px;
  }
}
